.navbar {
  position: relative;
  width: 100%;
  background-color: #34495e;
  color: white;
  font-size: 1rem;
  user-select: none;
}
.title {
  font-size: 1.5rem;
}
.buttons {
  margin-left: 16px;
  display: flex;
  flex-direction: row;
  color: white;
}
.list {
  color: rgb(216, 216, 216);
  background-color: transparent;
  border: none;
  margin-left: 10px;
  margin-right: 10px;
}

.list:hover {
  cursor: pointer;
  color: white;
}

#nav-dropdown {
  color: rgb(216, 216, 216);
}

#nav-dropdown:hover {
  color: white;
}

.disabled {
  cursor: not-allowed;
  color: grey;
  margin-left: 10px;
}

.disabled:hover {
  color: #aaa;
}
